<template>
    <div class="manager">
        <div class="field-part">
            <info-list :dataCopy="formData" :map="map" :filter="filter"></info-list>
        </div>
        <div class="field-part">
            <mt-field label="" v-model="formData.mark"></mt-field>
        </div>
        <div class="field-warn">
            
        </div>
        <!--  -->
        <div class="field-action">
            <mt-button type="default" size="large" @click="doSave"></mt-button>
        </div>
    </div>
</template>

<script>
    import { Toast } from 'mint-ui-rem-dpr';
    import * as services from '@/services/life-cell';
    import InfoList from '../info-list';
    export default {
        name: 'manager',
        components: {
            InfoList
        },
        data() {
            return {
                formData: {
                    name: ''
                },
                map: {
                    companyName: '',
                    no: '',
                    name: ''
                },
                filter: ['item', 'href', 'area', 'id', 'company', 'mark']
            };
        },
        created() {
            this.formData = Object.assign({}, this.$route.params.info);
        },
        methods: {
            doSave() {
                this.$router.go(-1);
            }
        }
    };

</script>

<style lang="scss" scoped>

</style>


